'use strict';

class Modules {
	constructor() {
		this.list = {};
	}

	register(module, element) {
		if (!this.list[module.name]) {
			this.list[module.name] = module;
			this.list[module.name].nodes = [element];
		} else {
			this.list[module.name].nodes.push(element);
		}

		Veams.Vent.trigger(Veams.EVENTS.moduleRegistered, {
			module: module,
			el: element
		});
	}

	load(obj) {}
}

module.exports = function () {
	if (!window.Veams) {
		console.error('Veams is not added to window!');
		return;
	}

	return new Modules();
};


